/**  */
export function calculateEnemyDef(statsObj: TStats, opt_ignoreDef = 0) { // , 
    try {
        const level = statsObj[''] ?? 0;
        const enemyLevel = statsObj[''] ?? 0;
        const calcIgnoreDef = opt_ignoreDef / 100;
        const calcDef = Math.max(-100, statsObj[''] ?? 0) / 100;    //  -100%
        const result = (level + 100) / ((1 - calcIgnoreDef) * (1 + calcDef) * (enemyLevel + 100) + level + 100);
        return result;
    } catch (error) {
        console.error(error);
        console.error(statsObj, opt_ignoreDef);
        throw error;
    }
}

/** () */
function calculateRes(res: number) {
    if (res == Infinity) return 0;  // 
    let result = res;
    if (result < 0) {
        result = 100 - result / 2;
    } else if (result < 75) {
        result = 100 - result;
    } else {
        result = 10000 / (4 * result + 100)
    }
    result /= 100;
    return result;
}

/** () */
export function calculateEnemyRes(element: string, statsObj: TStats) {
    try {
        const statName = '' + element + (element != '' ? '' : '') + '';
        const result = calculateRes(statsObj[statName] ?? 0);
        return result;
    } catch (error) {
        console.error(error);
        console.error(element, statsObj);
        throw error;
    }
}

